body {
  margin: 0;
  padding: 0;
  background-size: 100%;
  background-color :#191b2b;
  color: #c7c9cf;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-wrapper{
  background-color:#3c3d58;
}

.input-field{
    background-color: #191b2b;
}
.card{
  background-color: #272a43;
  border-radius: 10px;
}

form{
  margin-top: 50px;
  padding: 20px;

}

form button{
  margin: 20px 0;
}

form h4{
  margin-bottom: 40px;
}

input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]){
  border-color: #ec407a;
  box-shadow: none;
}
.card-title{
  text-transform: uppercase;
}

#center{
  text-align: center;
  font-weight: bold;
  font-family: 'Courier New', Courier, monospace;
}

#title{
  font-family: 'Courier New', Courier, monospace;
  font-weight: 300;
}
.card-content{
  font-size: medium;
}

.not{
  margin-top: 20px;
}
.leftspace{
  padding-left: 20%;
}

.brand-logo{
 font-family: 'Playfair Display', serif;
}

.card-titlee{
  font-size: x-large;
}